import { useSelector } from "react-redux";
import "./Loader.css";

export const Loader = () => {
    const { isLoading } = useSelector((state) => state.app)
    return (
        <div className={isLoading ? "modal-overlay" : "hidden"}>
            <div className="loader-animation">
                <div></div>
                <div></div>
                <div></div>
                <div></div>
                <div></div>
                <div></div>
                <div></div>
                <div></div>
            </div>
        </div>
    );
};