import { Publish } from "@mui/icons-material";
import { useState } from "react";
import { useDispatch } from "react-redux";
import { fetchData, fetchDataSuccess } from "../redux/app/appRedux";
import { fileService } from "../services/file.service";




export const FileUploader = ({ cb, name }) => {
    const [image, setImage] = useState(null);
    const dispatch = useDispatch()

    const handleFileUpload = async (event) => {
        dispatch(fetchData())
        try {
            const selectedFile = event.target.files[0]
            const res = await fileService.upload(selectedFile)
            setImage(URL.createObjectURL(selectedFile));
            cb(res, name)
            dispatch(fetchDataSuccess())
        } catch (err) {
            console.log('Cannot upload image')
        }
    }

    return (
        <div className="contactUpload">
            <label htmlFor="file">
                <Publish />
                {image && <img src={image} className="contactUploadImg" alt="Uploaded" />}
                <input type="file" id="file" name={name} onChange={handleFileUpload} style={{ display: "none" }} />
            </label>
        </div>
    )
}